<template>
	<view :style="{paddingTop:$store.state.tabh}"> 
		<TabBar/>
		<AddNft /> 
		<!-- 筛选弹窗 -->
		<Bdialog :show='Bdialogshow' title='标题' buttonName='确定' @closeFn='closeFn' @Submit='Submit' ></Bdialog>
		<view class="content"> 
			<view class="top_box">
				<SearchAndLimit  @closeFn='closeFn' :showLimt='current!=3' />
				<view class="classification flexJBC">
					<view class="fontS30 relative fw6" v-for="(item,index) in tags" :key='index' :class="index+1==current?'active':''" @click="getList(index+1)">
						{{item}}
					</view>
				</view>
		   </view>
		   <List style='margin-top: 150rpx;' :list='list'/>
		</view>
	</view>
</template>
<script>
	import SearchAndLimit from '../../componentes/searchAndLimit.vue'
	import List from '../../componentes/list.vue'
	import AddNft from '../addNft/addNft.vue'
	export default{
		components:{SearchAndLimit,List,AddNft},
		data(){
			return{
				page:0,
				tags:["首发","寄售","盲盒"],
				current:1,
				Bdialogshow: false,
				list:[], //列表数据
			}
		},
		onLoad() {
			this.getList()
		},
		onReachBottom() {
			this.getList()
		},
		methods:{
			getList(val){
				if(val){
					this.current=val
					this.page=0 
				} 
				this.page++;
				this.$my.post("/index/nft/get_nft_by_category",{limit:10,page:this.page,category_id:1,sort:'',sales:'',chain_type:''}).then(res=>{
				      if(res.code==200){
						  this.list=res.data.data
					  }
			    })
			},
			
			tagclick(){},
			Submit(val) {
				console.log(val, '提交');
			},
			closeFn(val) {
				this.Bdialogshow = val;
			},
		}
	}
</script> 
<style lang="scss" scoped>
	
	.content{
		.top_box{
			position: fixed;top:0rpx; z-index: 9;background: #F4F5F7;width: 100%;left:50%;transform: translateX(-50%);padding: 28rpx 40rpx 0rpx;box-sizing: border-box;
		} 
		padding:35rpx 40rpx ;
		.classification{
			margin-top: 48rpx;
			width: 100%;
			padding: 0 84rpx 0;
			box-sizing: border-box;
			border-bottom: 1px solid #D4D4D4;
			view{
				padding: 0 0 30rpx;color: #999999;
			}
			.active:before{
				content: '';
				position: absolute;
				bottom: -4rpx;
				left: 50%;
				transform: translate(-50%);
				width: 100%;
				height: 6rpx;
				background: #0F172C;
				border-radius: 3rpx;
			}
			.active{color: #333333 !important;}
		}
	}
	
</style>